import { Components } from '@ionic/core';
import { defineCustomElement } from '@ionic/core/components/ion-skeleton-text';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpCartaoRespostaLista } from '../cartao-resposta-lista';

defineCustomElement();

const meta: Meta<Components.IonSkeletonText & TpCartaoRespostaLista & any> = {
  title: 'components/Core/Cartão resposta lista',
  argTypes: {
    ['tp-component']: {
      control: { type: 'select' },
      options: ['tp-cartao-resposta-item'],
      description: 'Define o skeleton a ser renderizado.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: 'string' },
      },
    },
    flex: {
      control: { type: 'boolean' },
      description: 'Define a estilização do componente.',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
  },
  args: {
    ['tp-component']: 'tp-cartao-resposta-item',
  },
};

export default meta;

type Story = StoryObj<Components.IonSkeletonText & TpCartaoRespostaLista & any>;

export const Skeleton: Story = {
  render: ({ ...args }) => {
    const itemTemplates = [];

    for (let index = 0; index < 31; index++) {
      itemTemplates.push(
        html`<ion-skeleton-text
          tp-component="${args['tp-component']}"
          animated
        ></ion-skeleton-text>`,
      );
    }

    return html`
      <tp-cartao-resposta-lista ?flex=${args.flex}>
        ${itemTemplates}
      </tp-cartao-resposta-lista>
    `;
  },
};
Skeleton.argTypes = {
  color: { table: { disable: true } },
};
